// PROGRESS HERO

.progress-hero {
  $self: &;

  .component__background {
    background-color: #000f13;
    background-image: url('../img/background-progress-hero.jpg');
    background-position: calc(100% + 120px) -120px;
    background-repeat: no-repeat;
    background-size: 1400px;

    @include bp($tablet) {
      background-position: 100% -80px;
    }
  }

  .component__content {
    color: $white;
    padding: 300px $hpad $vpad;

    @include bp($desktop) {
      padding-top: 150px;
    }
  }

  &__content {
    & + .progress-cta {
      margin-top: 40px;
    }

    @include bp($desktop) {
      max-width: 60%;
    }
  }

  &__headline {
    @include type(mh1);

    margin-bottom: 30px;
    padding-right: 40%;

    @include bp($tablet) {
      @include type(wh1);
    }

    @include bp($desktop) {
      padding: 0;
    }
  }

  &__copy {
    @include type(mp1);

    margin-bottom: 20px;

    @include bp($tablet) {
      @include type(wp1);
    }
  }

  &__cta {
    @include link--mono-arrow;
  }

  &--video {
    .component__background {
      background-image: url('../img/background-progress-hero-video.jpg');
    }

    .component__content {
      padding-top: 100px;

      @include bp($desktop) {
        padding-top: 250px;
      }
    }

    #{$self}__video {
      margin-bottom: $vpad;
      margin-top: $vpad;
    }

    #{$self}__content {
      @include bp($desktop) {
        display: grid;
        gap: $hpad;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        max-width: none;
      }
    }
  }
}
