// PROGRESS CTA

.progress-cta {
  $self: &;

  backdrop-filter: blur(20px);
  border: 1px solid $n500;
  border-radius: 24px;
  padding: $vpad $hpad;
  position: relative;

  &::after {
    @include grad-border;
  }

  @include bp($tablet) {
    padding: 50px 40px;
  }

  &.visible {
    #{$self}__bar {
      &::after {
        transform: skew(0, 12deg) scaleY(1);

        @include bp($tablet) {
          transform: scaleX(1);
        }
      }
    }
  }

  &__progress {
    column-gap: 20px;
    display: grid;
    grid-template-areas:
      "percent ."
      "bar     ."
      "bar     dollars"
      "bar     cta"
      "bar     .";
    grid-template-columns: 3fr 10fr;
    grid-template-rows: auto;
    justify-items: center;
    position: relative;
    row-gap: 10px;
    z-index: 1;

    @include bp($tablet) {
      align-content: center;
      column-gap: 80px;
      grid-template-areas:
        "dollars percent"
        "bar     bar";
      grid-template-columns: 1fr 1fr;
      justify-items: start;
    }
  }

  &__bar {
    align-items: flex-end;
    background-color: $white;
    display: flex;
    grid-area: bar;
    justify-content: center;
    padding: 4px;
    width: 24px;

    &::after {
      @include trans(transform, 0.7s, ease-out);

      background-color: $green;
      content: '';
      display: block;
      height: var(--progress);
      transform: skew(0, 12deg) scaleY(0);
      transform-origin: bottom;
      width: 100%;
    }

    @include bp($tablet) {
      height: 24px;
      justify-content: start;
      transform: skew(-12deg, 0);
      width: 100%;

      &::after {
        height: 100%;
        transform: scaleX(0);
        transform-origin: left;
        width: var(--progress);
      }
    }
  }

  &__cta {
    @include button;

    grid-area: cta;
    justify-self: flex-start;
    margin-bottom: 20px;

    @include bp($tablet) {
      display: none;
    }
  }

  &__funded-dollars {
    @include type(mp2);

    grid-area: dollars;
    padding: 50px 0;

    strong {
      @include type(mh3);

      color: $green;
    }

    @include bp($tablet) {
      @include type(wp2);

      padding: 0;

      strong {
        @include type(wp2);

        font-family: $primary-bold;
      }
    }
  }

  &__funded-percent {
    @include type(mh5);

    color: $green;
    grid-area: percent;
    text-align: center;

    @include bp($tablet) {
      @include type(wh5);

      font-family: $primary-reg;
      justify-self: flex-end;
    }
  }
}
