button[data-controls='prev'],
button[data-controls='next'] {
  appearance: none;
  background: none;
  border: none;
  position: relative;
  text-indent: -9999px;
  width: 34px;

  &::after {
    background-color: $white;
    background-image: url("data:image/svg+xml,%3Csvg width='28' height='11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8.52.4c-.267.693-.56 1.333-.88 1.92-.32.613-.68 1.187-1.08 1.72H28v3.28H6.56c.373.533.72 1.093 1.04 1.68.32.613.613 1.267.88 1.96H5.44C3.787 9.013 1.973 7.533 0 6.52V4.8C1.973 3.84 3.787 2.373 5.44.4h3.08Z' fill='#{url-friendly-colour($orange)}'/%3E%3C/svg%3E");
    background-position: 3px 11px;
    background-repeat: no-repeat;
    border-radius: 50%;
    color: $orange;
    content: '';
    display: block;
    height: 34px;
    left: 0;
    line-height: 1;
    position: absolute;
    top: 0;
    width: 34px;
  }

  &:disabled {
    opacity: 0.5;
  }
}

.tns-nav {
  button {
    @include trans(background-color);

    appearance: none;
    background-color: $n600;
    border: none;
    border-radius: 50%;
    height: 12px;
    margin: 0 6px;
    width: 12px;

    &.tns-nav-active {
      background-color: $white;
    }
  }
}
