// FORM
.form {
  display: grid;
  gap: 32px;

  @include bp($tablet) {
    grid-template-areas:
      'first message'
      'last message'
      'email message'
      '. submit';
  }

  &__input-wrapper {
    position: relative;
    width: 100%;
  }

  @include bp($tablet) {
    &__input--firstname {
      grid-area: first;
    }

    &__input--lastname {
      grid-area: last;
    }

    &__input--email {
      grid-area: email;
    }

    &__input--message {
      grid-area: message;
    }
  }

  &__label {
    @include trans(all);
    @include type(wp4);

    color: $n700;
    display: block;
    left: 10px;
    opacity: 0;
    position: absolute;
    top: -12px;
  }

  &__input {
    @include type(wp3);

    @include placeholder {
      color: $n700;
    }

    appearance: none;
    border: none;
    border-radius: 0;
    line-height: 1;
    padding: 10px;
    width: 100%;
  }

  &__input:placeholder-shown + &__label {
    transition: 0.2s ease-in-out;
    z-index: -1;
  }

  &__input:not(:placeholder-shown) + &__label,
  &__input:focus:not(:placeholder-shown) + &__label {
    opacity: 1;
    transform: translateY(-10px);
    z-index: 1;
  }

  &__input--textarea {
    line-height: 1.56;
    min-height: 200px;
  }

  &__notice {
    @include type(wp4);

    color: $n700;
    margin-top: 8px;

    @include bp($tablet) {
      position: absolute;
    }
  }

  &__input--submit {
    @include button;

    justify-self: left;

    @include bp($tablet) {
      grid-area: submit;
      justify-self: right;
      margin-top: 30px;
    }
  }
}
