// LINK BANNER
.link-banner {
  .component__background {
    background-color: $green;
  }

  .component__content {
    padding: $vpad $hpad;
  }

  &__link {
    @include type(mp3);

    color: $n900;

    @include bp($tablet) {
      @include type(wp3);
    }

    display: block;
    font-family: $primary-bold;

    @include hover {
      text-decoration: none;
    }
  }
}
