.image-card {
  $self: &;

  backdrop-filter: blur(20px);
  border: 1px solid $n500;
  border-radius: 0 0 24px 24px;
  color: $white;
  display: flex;
  flex-direction: column;
  position: relative;
  text-align: left;
  text-decoration: none;

  &::after {
    @include grad-border('bottom right');
  }

  @include hover {
    #{$self}__img img {
      transform: scale(1.05);
    }

    #{$self}__cta {
      background-color: $orange;
      color: $white;
    }
  }

  &__img {
    display: block;
    margin: -1px -1px 0 -1px;
    max-width: calc(100% + 2px);
    overflow: hidden;
    width: calc(100% + 2px);

    img {
      @include trans(transform);
    }
  }

  &__content {
    padding: $vpad $hpad;
  }

  &__tags {
    @include type(wl1);

    color: $green;
    line-height: 1.3;
    margin-bottom: 24px;
  }

  &__headline {
    @include type(wh5);

    margin-bottom: 10px;
  }

  &__copy {
    @include type(wp3);

    margin-bottom: 10px;
  }

  &__cta {
    @include link--mono-arrow;
    @include type(wb2);

    margin-bottom: 10px;
  }
}
