// PAGINATION
.pagination {
  @include type(mp3);

  margin-bottom: 45px;
  text-align: center;

  @include bp($tablet) {
    margin-bottom: 95px;
  }

  > * {
    @include trans(color);

    appearance: none;
    background-color: transparent;
    border: none;
    color: $n900;
    margin: 0;
    padding: 4px 8px;

    @include hover {
      color: $orange;
    }
  }

  .is-active {
    background-color: $n300;
  }

  .is-disabled {
    color: $n500;
  }
}
