// FAQ

.accordion {
  color: $n900;
  padding: $vpad $hpad;

  .component__content {
    max-width: $content-narrow-max-width;
  }

  @include bp($tablet) {
    padding-bottom: calc(#{$vpad} * 2);
    padding-top: calc(#{$vpad} * 2);
  }

  &__item {
    margin-bottom: 40px;

    @include bp($tablet) {
      margin-bottom: 60px;
    }
  }

  &__title-bar {
    margin-bottom: 10px;
  }

  &__trigger {
    appearance: none;
    background: none;
    border: none;
    display: block;
    margin: 0;
    outline: none;
    padding: 0;
    position: relative;
    width: 100%;

    &:focus-visible {
      outline: 3px solid $green;
    }
  }

  &__title {
    @include type(mh3);

    display: block;
    outline: none;
    padding-left: 30px;
    pointer-events: none;
    text-align: left;

    @include bp($tablet) {
      @include type(wh4);
    }
  }

  &__icon {
    &::before,
    &::after {
      background-color: $orange;
      content: '';
      display: block;
      height: 3px;
      left: 0;
      position: absolute;
      top: 12px;
      width: 15px;

      @include bp($tablet) {
        top: 18px;
      }
    }

    &::after {
      @include trans(transform);

      transform: rotate(90deg);
    }
  }

  .accordion__trigger:focus .accordion__icon,
  .accordion__trigger:hover .accordion__icon {
    border-color: hsl(216deg 94% 43%);
  }

  .accordion__trigger[aria-expanded="true"] .accordion__icon {
    &::after {
      transform: rotate(0);
    }
  }

  &__panel {
    padding-left: 30px;
  }

  &__copy {
    @include type(mp3);

    margin-bottom: 40px;

    @include bp($tablet) {
      @include type(wp3);
    }
  }

  &__link {
    a {
      @include link--mono-arrow;
    }
  }
}

/* For Edge bug https://developer.microsoft.com/en-us/microsoft-edge/platform/issues/4806035/ */
.accordion__panel[hidden] {
  display: none;
}
