.teaser {
  $self: &;

  display: grid;
  padding: $vpad $hpad;
  text-decoration: none;

  @include bp($tablet) {
    gap: 36px;
    grid-template-columns: 375px 1fr;
    margin-bottom: 10px;
  }

  @include hover {
    #{$self}__img img {
      transform: scale(1.05);
    }

    #{$self}__cta {
      background-color: $orange;
      color: $white;
    }
  }

  &__img {
    margin-bottom: 24px;
    overflow: hidden;

    img {
      @include trans(transform);
    }
  }

  &__tags {
    @include type(ml1);

    color: $orange;
    line-height: 1.3;
    margin-bottom: 10px;

    @include bp($tablet) {
      @include type(wl1);
    }
  }

  &__headline {
    @include type(mh4);

    color: $blue;
    margin-bottom: 10px;

    @include bp($tablet) {
      @include type(wh5);
    }
  }

  &__copy {
    @include type(mp3);

    color: $blue;
    margin-bottom: 10px;

    @include bp($tablet) {
      @include type(wp3);
    }
  }

  &__cta {
    @include link--mono-arrow;

    margin-bottom: 10px;
  }
}
