@mixin link--mono-arrow($color: $orange) {
  @include trans(all);
  @include type(ml2);

  color: $color;
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;

  &::after {
    content: '→';
    margin-left: 8px;
  }

  @include hover {
    background-color: $color;
    color: $white;
  }

  @include bp($tablet) {
    @include type(wb2);
  }
}

@mixin link--arrow($color: $black) {
  @include type(mp3);

  color: $color;
  display: inline-block;
  font-family: $primary-bold;
  text-decoration: underline $orange solid 2px;

  &::after {
    content: ' →';
  }

  @include hover {
    background-color: $orange;
    color: $white;
  }
}

.skip-link {
  left: 50%;
  position: absolute;
  transform: translateY(-200%);

  &:focus {
    transform: translateY(0%);
  }
}
