// IMAGE HERO

.image-hero {
  .component__background {
    background-color: $n900;
    background-image: url('../img/background-pattern-4.svg');
    background-position: 0 500%;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .component__content {
    color: $white;
    display: grid;
    padding: $vpad $hpad;

    @include bp($tablet) {
      gap: 60px;
      grid-template-columns: 1fr 2fr;
      padding-bottom: 50px;
      padding-top: 50px;
    }
  }

  &__eyebrow {
    @include type(ml1);

    color: $green;
    margin-bottom: 10px;

    @include bp($tablet) {
      @include type(wl1);
    }
  }

  &__headline {
    @include type(mh3);

    margin-bottom: 10px;

    @include bp($tablet) {
      @include type(wh4);
    }
  }

  &__copy {
    @include type(mp3);

    margin-bottom: 20px;

    @include bp($tablet) {
      @include type(wp2);
    }
  }

  &__cta {
    @include link--mono-arrow;

    margin-bottom: 40px;
  }
}
