// TYPOGRAPHY

// Mixin for the following type presets
// name        mobile    desktop      lh     font
// ------------------------------------------------------------------------
// HEADINGS
// h1            40px       68px    1.18     Helvetica Neue Bold
// h2            32px       56px    1.15     Helvetica Neue Bold
// h3            24px       44px    1.18     Helvetica Neue Bold
// h4            20px       32px    1.25     Helvetica Neue Medium
// h5            16px       24px    1.17     Helvetica Neue Medium
// h6            12px       18px    1.22     Helvetica Neue Medium

// PARAGRAPHS
// p1            24px       30px    1.47     Helvetica Neue Regular
// p2            20px       24px    1.38     Helvetica Neue Regular
// p3            16px       18px    1.55     Helvetica Neue Regular
// p4            13px       14px    1.42     Helvetica Neue Regular
// p5            14px       16px    1.25     Ubuntu Mono Regular

// LABELS
// l1            13px       15px    1.00     Ubuntu Mono Bold
// l2            12px       14px    1.00     Ubuntu Mono Bold

// BUTTONS
// b1            20px       22px    1.00     Ubuntu Mono Bold
// b2            16px       18px    1.00     Ubuntu Mono Bold

// QUOTE
// q1            20px       24px    1.50     Ubuntu Mono Italic

$primary-reg: 'NeueHelvetica55Roman', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
$primary-bold: 'NeueHelvetica75Bold', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
$secondary: 'Ubuntu Mono', monospace;

@mixin type($style: wp1) {
  @if $style == 'mh1' {
    font-family: $primary-bold;
    font-size: 40px;
    line-height: 1.18;
  }

  @if $style == 'wh1' {
    font-family: $primary-bold;
    font-size: 68px;
    line-height: 1.18;
  }

  @if $style == 'mh2' {
    font-family: $primary-bold;
    font-size: 32px;
    line-height: 1.15;
  }

  @if $style == 'wh2' {
    font-family: $primary-bold;
    font-size: 56px;
    line-height: 1.15;
  }

  @if $style == 'mh3' {
    font-family: $primary-bold;
    font-size: 24px;
    line-height: 1.18;
  }

  @if $style == 'wh3' {
    font-family: $primary-bold;
    font-size: 44px;
    line-height: 1.18;
  }

  @if $style == 'mh4' {
    font-family: $primary-bold;
    font-size: 20px;
    line-height: 1.25;
  }

  @if $style == 'wh4' {
    font-family: $primary-bold;
    font-size: 32px;
    line-height: 1.25;
  }

  @if $style == 'mh5' {
    font-family: $primary-bold;
    font-size: 16px;
    line-height: 1.17;
  }

  @if $style == 'wh5' {
    font-family: $primary-bold;
    font-size: 24px;
    line-height: 1.17;
  }

  @if $style == 'mh6' {
    font-family: $primary-bold;
    font-size: 12px;
    line-height: 1.22;
  }

  @if $style == 'wh6' {
    font-family: $primary-bold;
    font-size: 18px;
    line-height: 1.22;
  }

  @if $style == 'mp1' {
    font-family: $primary-reg;
    font-size: 24px;
    line-height: 1.47;
  }

  @if $style == 'wp1' {
    font-family: $primary-reg;
    font-size: 30px;
    line-height: 1.47;
  }

  @if $style == 'mp2' {
    font-family: $primary-reg;
    font-size: 20px;
    line-height: 1.38;
  }

  @if $style == 'wp2' {
    font-family: $primary-reg;
    font-size: 24px;
    line-height: 1.38;
  }

  @if $style == 'mp3' {
    font-family: $primary-reg;
    font-size: 16px;
    line-height: 1.55;
  }

  @if $style == 'wp3' {
    font-family: $primary-reg;
    font-size: 18px;
    line-height: 1.55;
  }

  @if $style == 'mp4' {
    font-family: $primary-reg;
    font-size: 13px;
    line-height: 1.42;
  }

  @if $style == 'wp4' {
    font-family: $primary-reg;
    font-size: 14px;
    line-height: 1.42;
  }

  @if $style == 'mp5' {
    font-family: $secondary;
    font-size: 14px;
    line-height: 1.25;
  }

  @if $style == 'wp5' {
    font-family: $secondary;
    font-size: 16px;
    line-height: 1.25;
  }

  @if $style == 'ml1' {
    font-family: $secondary;
    font-size: 13px;
    font-weight: 700;
    letter-spacing: 0.05em;
    line-height: 1;
  }

  @if $style == 'wl1' {
    font-family: $secondary;
    font-size: 15px;
    font-weight: 700;
    letter-spacing: 0.05em;
    line-height: 1;
  }

  @if $style == 'ml2' {
    font-family: $secondary;
    font-size: 12px;
    font-weight: 700;
    letter-spacing: 0.05em;
    line-height: 1;
  }

  @if $style == 'wl2' {
    font-family: $secondary;
    font-size: 14px;
    font-weight: 700;
    letter-spacing: 0.05em;
    line-height: 1;
  }

  @if $style == 'mb1' {
    font-family: $secondary;
    font-size: 20px;
    font-weight: 700;
    letter-spacing: 0.05em;
    line-height: 1;
  }

  @if $style == 'wb1' {
    font-family: $secondary;
    font-size: 22px;
    font-weight: 700;
    letter-spacing: 0.05em;
    line-height: 1;
  }

  @if $style == 'mb2' {
    font-family: $secondary;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0.05em;
    line-height: 1;
  }

  @if $style == 'wb2' {
    font-family: $secondary;
    font-size: 18px;
    font-weight: 700;
    letter-spacing: 0.05em;
    line-height: 1;
  }

  @if $style == 'mq1' {
    font-family: $secondary;
    font-size: 20px;
    font-style: italic;
    font-weight: 400;
    line-height: 1.5;
  }

  @if $style == 'wq1' {
    font-family: $secondary;
    font-size: 24px;
    font-style: italic;
    font-weight: 400;
    line-height: 1.5;
  }
}
