// MIXINS

// Breakpoint mixin
@mixin bp($size, $type: "min-width") {
  @media screen and ($type: #{$size}px) {
    @content;
  }
}

// Hover Mixin
// Only invoke hover states when needed
@mixin hover {
  @media (hover: hover) and (pointer: fine) {
    &:hover {
      @content;
    }
  }
}

// Transition mixin
@mixin trans($attribute: all, $timing: 0.2s, $easing: linear) {
  transition: $attribute $timing $easing;

  @media (prefers-reduced-motion) {
    transition: none;
  }
}

// Image Replacement mixin
@mixin ir ($img, $width, $height) {
  background-image: url("../img/#{$img}");
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  height: $height;
  text-indent: -9999px;
  width: $width;
}

// Gradient Border Effect
// Because this is mixed with rounded borders
// cannot do a traditional gradient border
@mixin grad-border($dir: 'top right') {
  background-image: linear-gradient(to #{$dir}, $black, transparent);
  border-radius: inherit;
  content: '';
  display: block;
  height: calc(100% + 2px);
  left: -1px;
  position: absolute;
  top: -1px;
  width: calc(100% + 2px);
  z-index: -1;
}

// Skewed Outline Pseudo Element
@mixin skew-box-pseudo($angle: -7.5, $dist: -10) {
  &::before {
    border: 2px solid $green;
    content: '';
    display: block;
    height: calc(100% - 2px);
    left: 0;
    position: absolute;
    top: 0;
    transform: skew(#{$angle}deg, 0) translate(#{$dist}px, #{$dist}px);
    width: calc(100% - 2px);
    z-index: 0;
  }
}

@mixin hide {
  left: -9999px;
  position: absolute;
  top: -9999px;
}

@mixin unhide {
  left: auto;
  position: initial;
  top: auto;
}

@mixin placeholder {
  &::-webkit-input-placeholder {
    @content;
  }

  &::-moz-placeholder {
    @content;
  }

  &:-moz-placeholder {
    @content;
  }

  &:-ms-input-placeholder {
    @content;
  }
}
