// FORM CTA
.form-cta {
  .component__background {
    background-color: $n200;
    background-image: url('../img/background-pattern-3.svg');
    background-position: 0 100%;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .component__content {
    max-width: 825px;
    padding: 40px $hpad;

    @include bp($tablet) {
      padding-bottom: 90px;
      padding-top: 90px;
    }
  }

  &__headline {
    @include type(mh3);

    margin-bottom: 30px;
    text-align: center;

    @include bp($tablet) {
      @include type(wh3);

      margin-bottom: 45px;
    }
  }
}
