// TEXT HERO

.text-hero {
  .component__background {
    background-color: $n200;
  }

  .component__content {
    padding: $vpad $hpad;

    @include bp($tablet) {
      padding-bottom: calc(#{$vpad} * 3);
      padding-top: calc(#{$vpad} * 3);
    }
  }

  &__text {
    @include type(mp2);

    @include bp($tablet) {
      @include type(wp1);
    }
  }
}
