// STORY HERO

.story-hero {
  .component__background {
    background-color: $n200;
    background-image: url('../img/background-pattern-4.svg');
    background-repeat: no-repeat;
    background-size: cover;
  }

  .component__content {
    max-width: $content-narrow-max-width;
    padding: $vpad $hpad;

    @include bp($tablet) {
      padding-top: 68px;
    }
  }

  .component__background--image {
    background: linear-gradient(180deg, $n200 0 50%, transparent 50% 100%);
    background-color: transparent;
  }

  .component__content--image {
    padding: 0 $hpad $vpad;
  }

  &__wrap {
    position: relative;
  }

  &__eyebrow {
    @include type(ml2);

    color: $orange;
    margin-bottom: 16px;
    text-transform: uppercase;

    @include bp($tablet) {
      @include type(wl2);
    }
  }

  &__title {
    @include type(mh3);

    color: $n900;
    margin-bottom: 16px;

    @include bp($tablet) {
      @include type(wh3);
    }
  }

  &__author {
    @include type(ml1);

    color: $n700;
    margin-bottom: 16px;

    &:last-of-type {
      margin-bottom: 0;
    }

    @include bp($tablet) {
      @include type(wl1);
    }
  }
}
