// SITE FOOTER

.site-footer {
  background-color: $n900;
  color: $white;
  text-align: center;

  @include bp($tablet) {
    text-align: left;
  }

  .component__content {
    padding: calc(#{$vpad} * 2) $hpad;

    @include bp($tablet) {
      display: grid;
      gap: $hpad;
      grid-template-areas:
        "info nav"
        "head head"
        "list list";
      grid-template-columns: 50% 50%;
    }

    @include bp($ldesktop) {
      column-gap: $hpad;
      grid-template-areas:
        "info head head"
        "info list nav";
      grid-template-columns: 1fr 2fr 1fr;
      row-gap: 0;
    }
  }

  &__info {
    margin-bottom: 46px;

    @include bp($tablet) {
      grid-area: info;
    }

    @include bp($ldesktop) {
      margin-bottom: 0;
    }
  }

  &__logo {
    @include ir('caltech-logo.svg', 200px, 48px);

    margin: 0 auto 15px;

    @include bp($tablet) {
      margin: 0 0 15px;
    }
  }

  &__address {
    @include type(mp3);

    margin-bottom: 24px;

    @include bp($tablet) {
      @include type(wp3);
    }
  }

  &__copyright {
    @include type(mp4);

    @include bp($tablet) {
      @include type(wp4);
    }
  }

  &__contacts {
    margin-bottom: 50px;

    @include bp($tablet) {
      grid-area: list;
      margin-bottom: 0;
    }
  }

  &__contacts-head {
    @include type(mh4);

    margin-bottom: 22px;

    @include bp($tablet) {
      @include type(wh5);

      grid-area: head;
      margin-bottom: 0;
    }

    @include bp($ldesktop) {
      margin-bottom: 22px;
    }
  }

  &__contact-list {
    @include bp($tablet) {
      columns: 2;
    }
  }

  &__contact-item {
    break-inside: avoid;
    margin-bottom: 30px;
  }

  &__contact-head {
    @include type(ml1);

    margin-bottom: 4px;

    @include bp($tablet) {
      @include type(wl1);
    }
  }

  &__contact-email {
    @include trans(color);
    @include type(mp3);

    color: inherit;
    display: block;
    text-decoration: none;

    @include hover {
      color: $orange;
    }

    @include bp($tablet) {
      @include type(wp3);
    }
  }

  &__nav {
    @include bp($tablet) {
      grid-area: nav;
    }
  }

  &__nav-list-item {
    margin-bottom: 12px;
  }

  &__nav-list-link {
    @include trans(color);
    @include type(mh4);

    color: inherit;
    text-decoration: none;

    @include hover {
      color: $orange;
    }

    @include bp($tablet) {
      @include type(wh6);
    }
  }
}
