// TAB SLIDER

// This component is a traditional slider for smaller screens
// and a tab interface for larger screens

.tab-slider {
  $self: &;

  .component__background {
    background-color: $white;
  }

  .component__content {
    padding: $vpad 0;

    @include bp($tablet) {
      padding: $vpad2 0;
    }
  }

  &__headline {
    @include type(mh2);

    margin-bottom: 15px;
    padding: 0 $hpad;
    text-align: center;

    @include bp($tablet) {
      @include type(wh2);

      margin-bottom: 0;
    }
  }

  &__copy {
    @include type(mp2);

    margin-bottom: 20px;
    padding: 0 $hpad;
    text-align: center;

    @include bp($tablet) {
      display: none;
    }
  }

  &__cards {
    @include bp($tablet) {
      display: grid;
      grid-template-areas: 'card';
      height: 100%;
    }
  }

  &__card {
    background-color: $n200;
    cursor: pointer;
    margin-top: 40px;
    padding-top: $vpad;
    position: relative;

    &::before {
      border-color: transparent transparent $n200 transparent;
      border-style: solid;
      border-width: 0 52px 40px 52px;
      content: '';
      display: block;
      height: 0;
      left: calc(50% - 52px);
      position: absolute;
      top: -40px;
      width: 0;
    }

    &:nth-of-type(1) {
      #{$self}__card-indicator {
        span:nth-of-type(1) {
          background-color: $orange;
        }
      }
    }

    &:nth-of-type(2) {
      #{$self}__card-indicator {
        span:nth-of-type(2) {
          background-color: $orange;
        }
      }
    }

    &:nth-of-type(3) {
      #{$self}__card-indicator {
        span:nth-of-type(3) {
          background-color: $orange;
        }
      }
    }

    &:nth-of-type(4) {
      #{$self}__card-indicator {
        span:nth-of-type(4) {
          background-color: $orange;
        }
      }
    }

    &:nth-of-type(5) {
      #{$self}__card-indicator {
        span:nth-of-type(5) {
          background-color: $orange;
        }
      }
    }

    @include bp($tablet) {
      align-content: flex-start;
      background-color: transparent;
      display: grid;
      grid-area: card;
      grid-template-columns: repeat(5, 20%);
      padding: 0;
      pointer-events: none;
      z-index: 1;

      &::before {
        display: none;
      }

      &.is-active {
        z-index: 2;

        #{$self}__card-label {
          &::after {
            content: '';
          }
        }

        #{$self}__card-headline {
          color: $orange;
        }

        #{$self}__card-icon {
          background-color: transparent;
          fill: $orange-light;

          &::after {
            transform: translate(0);
          }
        }

        #{$self}__card-content {
          opacity: 1;
        }
      }

      &:nth-of-type(1) {
        grid-template-areas:
          'label   .       .       .       .'
          'content content content content content';
      }

      &:nth-of-type(2) {
        grid-template-areas:
          '.       label   .       .       .'
          'content content content content content';
      }

      &:nth-of-type(3) {
        grid-template-areas:
          '.       .       label   .       .'
          'content content content content content';
      }

      &:nth-of-type(4) {
        grid-template-areas:
          '.       .       .       label   .'
          'content content content content content';
      }

      &:nth-of-type(5) {
        grid-template-areas:
          '.       .       .       .       label'
          'content content content content content';
      }
    }
  }

  &__card-label {
    appearance: none;
    background: transparent;
    border: none;
    margin: 0;
    padding: 0;
    position: relative;
    width: 100%;

    &::after {
      border-color: transparent transparent $n200 transparent;
      border-style: solid;
      border-width: 0 52px 40px 52px;
      bottom: -70px;
      content: '';
      display: block;
      height: 0;
      left: calc(50% - 52px);
      position: absolute;
      width: 0;
    }

    @include hover {
      #{$self}__card-icon {
        background-color: transparent;
        fill: $orange-light;

        &::after {
          transform: translate(0);
        }
      }
      #{$self}__card-headline {
        color: $orange;
      }
    }

    @include bp($tablet) {
      cursor: pointer !important;
      display: block;
      grid-area: label;
      pointer-events: auto;

      &::after {
        content: none;
      }
    }

    &:focus-visible {
      outline-offset: -3px;
    }
  }

  &__card-icon {
    display: none;

    @include bp($tablet) {
      @include trans(all);

      background-color: $orange-light;
      border-radius: 50%;
      display: block;
      fill: $white;
      height: 60px;
      margin: 10px auto 16px;
      position: relative;
      width: 60px;

      &::after {
        @include trans(transform);

        border: 2px solid $green;
        border-radius: 50%;
        content: '';
        display: block;
        height: calc(100% - 4px);
        left: 0;
        position: absolute;
        top: 0;
        transform: translate(-4px, -4px);
        width: calc(100% - 4px);
      }

      svg {
        height: 100%;
        width: 100%;
      }
    }

    @include bp($desktop) {
      height: 90px;
      width: 90px;
    }
  }

  &__card-headline {
    @include trans(color);
    @include type(wh5);

    color: $orange;
    margin-bottom: 10px;
    padding: 0 50px;
    text-align: center;

    @include bp($tablet) {
      @include type(mh5);

      color: $n900;
      padding: 0 15px;
    }

    @include bp($desktop) {
      @include type(wh5);
    }
  }

  &__card-stats {
    display: none;

    @include bp($tablet) {
      @include type(wp4);

      color: $n700;
      display: block;
      text-align: center;
    }
  }

  &__card-indicator {
    span {
      background-color: $n500;
      border-radius: 50%;
      display: inline-block;
      height: 12px;
      margin: 0 5px;
      width: 12px;
    }

    @include bp($tablet) {
      display: none;
    }
  }

  &__card-content {
    background-color: $n200;
    margin: 0 auto 30px;
    max-width: 400px;
    padding: 40px;
    pointer-events: auto;
    position: relative;

    @include bp($tablet) {
      @include trans(opacity);

      align-items: flex-start;
      display: grid;
      gap: 64px;
      grid-area: content;
      grid-template-columns: 1fr 1fr;
      height: 100%;
      margin: 70px auto 0;
      max-width: none;
      opacity: 0;
      padding: 80px 64px 64px;
    }
  }

  &__card-image-wrap {
    @include skew-box-pseudo(0);

    margin-bottom: 40px;
    position: relative;
    transform: skew(-7.5deg, 0);

    @include bp($tablet) {
      @include skew-box-pseudo(0, -16);

      align-self: flex-start;
      margin: 0 30px;
    }
  }

  &__card-image {
    overflow: hidden;

    img {
      object-fit: cover;
      transform: skew(7.5deg, 0) scale(112%);
    }
  }

  &__card-rte {
    h2 {
      @include type(wl1);

      margin-bottom: 10px;
    }

    p {
      @include type(mp3);

      margin-bottom: 1em;
    }

    a {
      @include link--arrow;

      justify-self: flex-start;
    }

    ul {
      li {
        display: grid;
        gap: 4px;
        grid-template-columns: 10px 1fr;
        margin-bottom: 10px;

        &::before {
          background-color: $black;
          border-radius: 50%;
          content: '';
          display: block;
          height: 6px;
          position: relative;
          top: 9px;
          width: 6px;
        }
      }
    }
  }

  .tns-outer {
    margin: 0 30px;
    position: relative;
  }

  .tns-controls {
    display: flex;
    position: absolute;
    top: 118px;
    width: 100%;
    z-index: 1;

    button[data-controls='next'] {
      margin-left: auto;

      &::after {
        transform: scaleX(-1);
      }
    }
  }
}
