// SITE HEADER
.site-header {
  align-items: center;
  background-color: $n900;
  display: flex;
  filter: drop-shadow(0 4px 4px rgba($black, 0.25));
  justify-content: space-between;
  padding: 20px $hpad;
  position: relative;
  z-index: 100;

  @include bp($xldesktop) {
    padding-left: $hpad2;
    padding-right: $hpad2;
  }

  .page-type-home & {
    background-color: rgba($n900, 0.6);
    left: 0;
    position: absolute;
    top: 0;
    width: 100vw;
    z-index: 1000;

    @include bp($tablet) {
      backdrop-filter: blur(24px);
      background-color: rgba($n900, 0.16);
    }
  }

  &__branding {
    @include ir('caltech-initiative-for-students-logo.svg', 191px, 32px);
  }

  &__menu-toggle {
    appearance: none;
    background: none;
    border: none;
    height: 28px;
    position: relative;
    width: 28px;

    @include bp($desktop) {
      display: none;
    }

    span,
    span::before,
    span::after {
      @include trans(all);

      background-color: $white;
      border-radius: 5000px;
      content: '';
      display: block;
      height: 3px;
      left: 0;
      position: absolute;
    }

    span {
      top: 13px;
      width: 29px;
    }

    span::before {
      top: -9px;
      width: 20px;
    }

    span::after {
      top: 9px;
      width: 14px;
    }
  }

  &__nav {
    background-color: $white;
    box-shadow: 0 4px 4px rgba($n900, 0.25);
    color: $n900;
    display: none;
    left: 0;
    line-height: initial;
    margin-top: 20px;
    padding: 0 24px 32px;
    position: absolute;
    width: 100vw;

    @include bp($desktop) {
      background-color: transparent;
      box-shadow: none;
      color: $white;
      display: flex;
      left: unset;
      margin: 0;
      padding: 0;
      position: relative;
      width: auto;
    }
  }

  &__nav-list {
    margin-bottom: 15px;

    @include bp($desktop) {
      align-items: center;
      display: flex;
      margin-bottom: 0;
    }
  }

  &__nav-list-item {
    border-bottom: 1px solid $green;
    line-height: 1;

    &:last-of-type {
      border: none;
    }

    @include bp($desktop) {
      border: none;
      margin-right: 40px;
    }
  }

  &__nav-list-link {
    @include type(mh4);

    color: $n900;
    display: block;
    padding: 24px 0;
    text-decoration: none;

    @include bp($desktop) {
      @include trans(color);
      @include type(wh6);

      color: $white;
      padding: 0;

      @include hover {
        color: $orange;
      }
    }
  }

  &__nav-list-button {
    @include button;

    width: 100%;

    @include bp($desktop) {
      width: auto;
    }
  }

  &__menu {
    line-height: 0;

    &.menu-open {
      .site-header__menu-toggle {
        span {
          transform: rotate(45deg);
          transform-origin: center;

          &::before {
            transform: rotate(-90deg) translate(-2px, 7px);
            transform-origin: center;
            width: 15px;
          }

          &::after {
            transform: rotate(90deg) translate(-2px, -7px);
            transform-origin: center;
            width: 15px;
          }
        }
      }

      .site-header__nav {
        display: block;

        @include bp($desktop) {
          display: flex;
        }
      }
    }
  }
}
