// IMAGE CTA

.image-cta {
  overflow: hidden;

  .component__background {
    @include bp($tablet-max, 'max-width') {
      height: auto;
      left: unset;
      position: relative;
      top: unset;
      width: auto;
    }
  }

  .component__content {
    @include bp($tablet) {
      display: flex;
      justify-content: flex-end;
      padding: 60px;
    }
  }

  &__picture {
    display: block;
    position: relative;
    z-index: -1;

    img {
      display: block;
    }

    @include bp($tablet) {
      display: flex;
      height: 100%;

      img {
        object-fit: cover;
        object-position: top center;
      }
    }
  }

  &__content {
    background-color: $n200;
    margin: -60px $hpad $vpad;
    padding: $vpad $hpad;
    position: relative;

    @include skew-box-pseudo(-3.5);

    @include bp($tablet) {
      margin: 0;
      max-width: 48%;
      padding: $vpad 60px 60px;
      position: relative;
      transform: skew(-3.5deg, 0);

      &::before {
        transform: translate(-14px, -14px);
      }
    }
  }

  &__content-wrap {
    @include bp($tablet) {
      transform: skew(3.5deg, 0);
    }
  }

  &__headline {
    @include type(mh2);

    margin-bottom: 20px;

    @include bp($tablet) {
      @include type(wh2);
    }
  }

  &__copy {
    @include type(wp3);

    @include bp($tablet) {
      @include type(wp2);
    }
  }
}
