// STATS
.stats {
  .component__content {
    padding: $vpad $hpad;

    @include bp($desktop) {
      align-items: center;
      display: flex;
      padding-bottom: 96px;
      padding-top: 96px;
    }
  }

  &__info {
    text-align: center;

    @include bp($desktop) {
      margin-right: 40px;
      max-width: 300px;
      text-align: left;
    }
  }

  &__headline {
    @include type(mh3);

    margin-bottom: 15px;

    @include bp($desktop) {
      @include type(wh4);
    }
  }

  &__copy {
    @include type(wp3);

    margin-bottom: 35px;
  }

  &__cta {
    @include button;

    margin-bottom: 50px;
  }

  &__stat {
    margin-bottom: 30px;
    text-align: center;
  }

  &__stat-graphic {
    margin-bottom: 15px;
    max-width: 200px;
  }

  &__stat-copy {
    @include type(wp5);

    margin-inline: auto;
    max-width: 300px;
    padding: 0 15px;
  }

  .tns-outer {
    margin: 0 30px;
    position: relative;
  }

  .tns-controls {
    display: flex;
    position: absolute;
    top: 25%;
    width: 100%;
    z-index: 1;

    &:focus-visible {
      left: -30px;
      padding: 0 30px;
      width: calc(100% + 60px);
    }
  }

  button[data-controls='prev'] {
    &::after {
      transform: translateX(-30px);
    }
  }

  button[data-controls='next'] {
    margin-left: auto;

    &::after {
      transform: scaleX(-1) translateX(-30px);
    }
  }
}
