// COMPONENT BASE STYLING

.component {
  position: relative;

  &__background {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
  }

  &__content {
    margin-left: auto;
    margin-right: auto;
    max-width: calc(#{$content-max-width} + #{$hpad} + #{$hpad});
    position: relative;
    z-index: 2;

    @include bp($tablet) {
      max-width: calc(#{$content-max-width} + #{$hpad} + #{$hpad});
    }
  }
}
