// RICH TEXT EDITOR
.component--rte {
  margin-inline: auto;
  max-width: $content-narrow-max-width;
  padding: $vpad $hpad calc(#{$vpad} * 2);
}

.rte {
  color: $n900;

  h1 {
    @include type(mh3);

    margin-bottom: 10px;

    @include bp($tablet) {
      @include type(wh3);
    }
  }

  h2 {
    @include type(mh4);

    margin-bottom: 10px;

    @include bp($tablet) {
      @include type(wh4);
    }
  }

  h3 {
    @include type(mh5);

    margin-bottom: 10px;

    @include bp($tablet) {
      @include type(wh5);
    }
  }

  h4 {
    @include type(mh6);

    margin-bottom: 10px;

    @include bp($tablet) {
      @include type(wh6);
    }
  }

  h5 {
    @include type(mh5);

    margin-bottom: 10px;

    @include bp($tablet) {
      @include type(wh5);
    }
  }

  h6 {
    @include type(mh6);

    margin-bottom: 10px;

    @include bp($tablet) {
      @include type(wh6);
    }
  }

  p {
    @include type(mp3);

    margin-bottom: 30px;

    @include bp($tablet) {
      @include type(wp3);
    }
  }

  ul {
    list-style: disc;
    margin: 20px 0 20px 30px;
  }

  ol {
    list-style: decimal;
    margin: 20px 0 20px 30px;
  }

  li {
    @include type(mp3);

    margin-bottom: 10px;

    @include bp($tablet) {
      @include type(wp3);
    }
  }

  em {
    font-family: 'NeueHelvetica56Italic', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
  }

  strong {
    font-family: $primary-bold;
  }

  em strong,
  strong em {
    font-family: 'NeueHelvetica76BoldItalic', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
  }

  a {
    color: inherit;
    text-decoration: underline;

    @include hover {
      text-decoration: none;
    }
  }

  blockquote {
    @include skew-box-pseudo(-3.5);

    background-color: $n200;
    font-family: $secondary;
    font-size: 20px;
    font-style: italic;
    line-height: 1.5;
    padding: 20px;
    position: relative;

    @include bp($tablet) {
      @include skew-box-pseudo(-7.5);

      font-size: 24px;
    }
  }

  .video-container {
    margin: 40px 0;
  }

  .button {
    @include button;

    margin: 0 30px 30px 0;
  }
}
