// CARDS CTA

.cards-cta {
  $self: &;

  .component__background {
    background-color: $n900;
    background-image: url('../img/background-pattern-1.svg');
    background-position: 50% 0;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .component__content {
    color: $white;
    padding: $vpad $hpad;
    text-align: center;

    @include bp($tablet) {
      padding-bottom: 95px;
      padding-top: 70px;
    }
  }

  &__headline {
    @include type(mh2);

    margin-bottom: 50px;
    margin-top: 24px;

    @include bp($tablet) {
      @include type(wh2);

      margin-bottom: 70px;
    }
  }

  &__cards {
    .image-card {
      @include bp($tablet) {
        @include trans(all, 0.75s, ease-in-out);

        opacity: 0;
        transform: translateY(100px);

        &:focus-visible {
          transition: none;
        }
      }
    }

    @include bp($tablet) {
      column-gap: $hpad;
      display: grid;
      grid-template-columns: repeat(3, minmax(0, 1fr));
      margin-bottom: 70px;
    }

    @include bp($desktop) {
      column-gap: $hpad2;
    }
  }

  &__cta {
    @include button;
  }

  &.visible {
    .image-card {
      opacity: 1;
      transform: none;
    }

    .image-card:nth-of-type(1) {
      transition-delay: 0.1s;
    }

    .image-card:nth-of-type(2) {
      transition-delay: 0.3s;
    }

    .image-card:nth-of-type(3) {
      transition-delay: 0.5s;
    }
  }

  .tns-nav {
    margin-bottom: 40px;
  }
}
